@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap");

* {
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

body {
  margin: 0;
}

p {
  margin: 0;
}

input[type="range"] {
  outline: none;
  background: #d9d9d9;
  -webkit-appearance: none;
  height: 7px;
  width: 100%;
}

/* Below logic cannot be combined with commas. See for reference: https://css-tricks.com/sliding-nightmare-understanding-range-input/ */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border: 0;
}

input[type="range"]::-ms-thumb {
  -webkit-appearance: none;
  height: 31px;
  width: 31px;
  background: #17c690;
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

input[type="range"]::-webkit-slider-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-webkit-slider-thumb:active {
  cursor: grabbing;
}

input[type="range"]::-moz-range-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-moz-range-thumb:active {
  cursor: grabbing;
}

input[type="range"]::-ms-thumb:hover {
  cursor: pointer;
}

input[type="range"]::-ms-thumb:active {
  cursor: grabbing;
}

#bottom-container {
  display: none;
}

#hero-container {
  background: #17c690;
  padding: 150px 140px 250px 100px;
  display: flex;
  justify-content: space-between;
}

#hero-text-container * {
  color: white;
}
#hero-text-container h1 {
  font-size: 64px;
  font-weight: 600;
  line-height: 40px;
  white-space: nowrap;
}

#hero-text-container p {
  max-width: 730px;
  font-size: 32px;
  font-weight: 300;
  line-height: 48px;
  margin-top: 74px;
}

#categories-section {
  transform: translateY(-120px);
  border-radius: 132px;
  display: flex;
  flex-direction: column;
  padding: 104px 155px 0 120px;
  background: white;
}

.category-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 76px;
}

.category-wrapper:first-child {
  margin-top: 0;
}

.category-title {
  letter-spacing: 1px;
  margin-bottom: 32px;
}

.category-bottom-container {
  display: flex;
  margin-top: 40px;
}

.slider-wrapper {
  position: relative;
  margin-left: 60px;
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.slider-tooltip {
  position: absolute;
  top: -6px;
  left: 17px;
  transform: translate(-50%, -50%);
  width: 76px;
  height: 25px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 150%;
  background: white;
  padding-top: 2px;
}

.slider-tooltip::before {
  position: absolute;
  bottom: -17px;
  left: 30px;
  content: "";
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 14px solid #d9d9d9;
  transform: rotate(270deg);
}

#count-btn {
  background: #17c690;
  border-radius: 50px;
  border: 0;
  padding: 22px 64px;
  font-weight: 700;
  font-size: 16px;
  color: white;
  cursor: pointer;
  width: 168px;
  margin-top: 90px;
  align-self: flex-end;
}

#co2-summed-up-result-section {
  background: #424242;
  border-radius: 10px;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 110px 0;
}

#co2-summed-up-result-section p {
  font-weight: 600;
  font-size: 64px;
  color: white;
}

#co2-result {
  color: #17c690 !important;
}

.horizontal-spacing {
  padding-right: 155px;
  padding-left: 155px;
}

#carbon-footprint-section {
  background: #17c690;
  padding: 68px 114px 100px 120px;
  border-radius: 132px;
}

#carbon-footprint-section hr {
  width: 100px;
  height: 4px;
  background: white;
  border: 0;
  margin: 0;
}

#carbon-footprint-section p {
  margin-top: 40px;
  line-height: 24px;
  font-size: 16px;
  color: white;
  font-weight: 300;
}

#carbon-footprint-paragraphs {
  max-width: 780px;
}

#carbon-footprint-container {
  display: flex;
  justify-content: space-between;
}

#carbon-footprint-container img {
  align-self: flex-end;
}

#partners-logos-section {
  background: #17c690;
}

#partners-logos-container {
  background-color: white;
  width: 100vw;
  border-bottom-left-radius: 132px;
  border-bottom-right-radius: 132px;
  display: flex;
  justify-content: space-between;
  padding: 38px 114px 0 120px;
  align-items: center;
}

#partners-logos-container img:first-child {
  width: 248px;
  height: 129px;
}

#partners-logos-container img:nth-child(2) {
  width: 377px;
  height: 377px;
}

#partners-logos-container img:nth-child(3) {
  width: 156px;
  height: 156px;
}

#partners-logos-container img:nth-child(4) {
  width: 270px;
  height: 206px;
}
